

export function description(text,lang = 'ru'){
	/*$session = Yii::$app->session;
        if(isset($_GET['lang'])&&in_array($_GET['lang'], ['ru', 'en', 'de', 'sr']))$lang = Html::encode($_GET['lang']);
            elseif (NULL!==$session->get('lang')) $lang = $session->get('lang');
                else {
                    $lang = getallheaders()['Accept-Language'];
                    $lang = substr($lang, 0,2);//это необходимо, т.к.,например, chrome из getallheaders()['Accept-Language'] возвращает сложную строку
                }*/
		switch (lang) {
			case 'ru':
				switch (text) {
					case 'tara':
					return 'Черногория не только прекрасная, но и во многом уникальная страна! Более 60% территории имеют вершины выше 1000м над уровнем моря, а каньон реки Тара, протекающей в национальном парке Дурмитор - самый глубокий в Европе.';
					
					case 'scadar':
					return 'Скадарское озеро на границе Черногории и Албании имеет поверхность располагающуюся на 6 м ниже уровня моря. Река Бояна- единственная река в мире, которая течет вверх по течению.';
					case 'kotor':
					return 'А река Гурвич на южной стороне Старого Котора вообще не имеет русла: в дождливые дни река выходит из люка пещеры и возвращает морскую воду, которая впадает, как залив, в море. Когда он высыхает, Гурдич теряется в безднах горы Ловчен во внутренних районах Которского залива, а его бассейн наполняется морской водой. Гурдич - это природное явление, называемое эставел. Кстати,  Котор - город возникший до Рождества Христова. Один из его соборов, собор Святого Трифона, на 50 лет старше Собора Парижской Богоматери.';
					case 'petrovich':
					return 'Одно из старейших оливковых деревьев в мире (ему более 2000 лет) растет в Черногорском городе Бар, где находятся еще более 10000 олив возрастом более 1000 лет. А самый высотный мавзолей в мире(1657м над уровнем моря) - мавзолей Петровича расположен на горе Ловчен в одноименном национальном парке Черногории.';
					case 'ostrog':
					return 'В монастыре Острог высеченном  в склоне горы сохраняются частицы Святого Креста и руки Иоанна Крестителя. И еще три известные христианские реликвии находятся в Цетинском монастыре. Это рука Иоанна Крестителя, частица Креста Господня и икона Пресвятой Богородицы Филермоса.';
					case 'plaza':
					return 'А уж по разнообразию и протяженности пляжей Черногория точно не уступает другим курортным направлениям.';
				
				default:
					return '';

			}


		case 'en':
		switch (text) {
					case 'tara':
					return 'Montenegro is not only beautiful, but in many ways a unique country! More than 60% of the territory has peaks above 1000 m above sea level, and the canyon of the Tara River, flowing in the Durmitor National Park, is the deepest in Europe.';
					
					case 'scadar':
					return 'Lake Skadar on the border of Montenegro and Albania has a surface 6 m below sea level. The Boyana River is the only river in the world that flows upstream.';
					case 'kotor':
					return 'And the Gurdich River on the southern side of Old Kotor has no riverbed at all: on rainy days, the river leaves the cave hatch and returns seawater, which flows like a bay into the sea. When it dries up, Gurdich is lost in the depths of Mount Lovcen in the interior of the Bay of Kotor, and its basin is filled with sea water. Gurdich is a natural phenomenon called estavel. By the way, Kotor is a city that emerged before the birth of Christ. One of its cathedrals, the Cathedral of Saint Tryphon, is 50 years older than Notre Dame Cathedral.';
					case 'petrovich':
					return 'One of the oldest olive trees in the world (over 2000 years old) grows in the Montenegrin town of Bar, where there are more than 10,000 olive trees over 1000 years old. And the highest mausoleum in the world (1657m above sea level) - the Petrovich mausoleum is located on Mount Lovcen in the national park of the same name in Montenegro.';
					case 'ostrog':
					return 'In the monastery of Ostrog, carved into the side of the mountain, particles of the Holy Cross and the hands of John the Baptist are preserved. And three more famous Christian relics are in the Cetinje Monastery. This is the hand of John the Baptist, a particle of the Cross of the Lord and an icon of the Most Holy Theotokos of Filermos.';
					case 'plaza':
					return 'And in terms of the variety and length of beaches, Montenegro is definitely not inferior to other resort destinations.';
				
				default:
					return '';
			}
			
		
		case 'sr':
		switch (text) {
					case 'tara':
					return 'Црна Гора није само лијепа, већ и на много начина јединствена држава! Више од 60% територије има врхове више од 1000 м надморске висине, а кањон реке Таре, који тече у Националном парку Дурмитор, најдубљи је у Европи.';
					
					case 'scadar':
					return 'Скадарско језеро на граници Црне Горе и Албаније има површину 6 м испод нивоа мора. Река Бојана је једина река на свету која тече узводно.';
					case 'kotor':
					return 'А река Гурвицх на јужној страни Старог Котора уопште нема канал: током кишних дана река напушта отвор пећине и враћа морску воду која попут залива тече у море. Кад пресуши, Гурдицх се губи у дубини планине Ловћен у унутрашњости Боке Которске, а његов базен се пуни морском водом. Гурдицх је природни феномен који се назива еставел. Иначе, Котор је град који је настао пре Христовог рођења. Једна од његових катедрала, катедрала Светог Трипуна, старија је 50 година од катедрале Нотре Даме.';
					case 'petrovich':
					return 'Једно од најстаријих стабала маслина на свијету (старо преко 2000 година) расте у црногорском граду Бару, гдје има више од 10.000 стабала маслина старијих од 1000 година. И највиши маузолеј на свијету (1657м надморске висине) - маузолеј Петрович налази се на планини Ловћен у истоименом националном парку у Црној Гори.';
					case 'ostrog':
					return 'У манастиру Острог, урезаном у страну планине, сачуване су честице Часног крста и руке Јована Крститеља. И још три познате хришћанске реликвије налазе се у Цетињском манастиру. Ово је рука Јована Крститеља, честица Крста Господњег и икона Пресвете Богородице из Филермоса.';
					case 'plaza':
					return 'А по разноликости и дужини плажа, Црна Гора дефинитивно није инфериорна у односу на друге одмаралишта.';
				
				default:
					return '';
			}
			
		case 'de':
		switch (text) {
					case 'tara':
					return 'Montenegro ist nicht nur schön, sondern in vielerlei Hinsicht ein einzigartiges Land! Mehr als 60% des Territoriums weisen Gipfel auf, die höher als 1000 m über dem Meeresspiegel liegen, und der Canyon des Tara-Flusses, der im Durmitor-Nationalpark fließt, ist der tiefste in Europa.';
					
					case 'scadar':
					return 'Der Skadar-See an der Grenze zwischen Montenegro und Albanien hat eine Oberfläche 6 m unter dem Meeresspiegel. Der Boyana River ist der einzige Fluss der Welt, der flussaufwärts fließt.';
					case 'kotor':
					return 'Und der Gurvich-Fluss an der Südseite von Stary Kotor hat überhaupt keinen Kanal: An Regentagen verlässt der Fluss die Höhlenluke und gibt Meerwasser zurück, das wie eine Bucht ins Meer fließt. Wenn es austrocknet, ist Gurdich in den Tiefen des Mount Lovcen im Inneren der Bucht von Kotor verloren und sein Pool ist mit Meerwasser gefüllt. Gurdich ist ein natürliches Phänomen namens Estavel. Kotor ist übrigens eine Stadt, die vor der Geburt Christi entstanden ist. Eine ihrer Kathedralen, die Kathedrale von Saint Tryphon, ist 50 Jahre älter als die Kathedrale Notre Dame.';
					case 'petrovich':
					return 'Einer der ältesten Olivenbäume der Welt (über 2000 Jahre alt) wächst in der montenegrinischen Stadt Bar, in der mehr als 10.000 über 1000 Jahre alte Olivenbäume stehen. Und das höchste Mausoleum der Welt (1657 m über dem Meeresspiegel) - das Petrovich-Mausoleum befindet sich auf dem Berg Lovcen im gleichnamigen Nationalpark in Montenegro.';
					case 'ostrog':
					return 'Im Kloster Ostrog, das in die Seite des Berges gehauen ist, sind Partikel des Heiligen Kreuzes und die Hände Johannes des Täufers erhalten. Drei weitere berühmte christliche Relikte befinden sich im Kloster Cetinje. Dies ist die Hand von Johannes dem Täufer, ein Teilchen des Kreuzes des Herrn und eine Ikone des Allerheiligsten Theotokos von Filermos.';
					case 'plaza':
					return 'Und in Bezug auf die Vielfalt und Länge der Strände ist Montenegro anderen Urlaubszielen definitiv nicht unterlegen.';
				
				default:
					return '';
			}
			
		default:
					return '';
	}

		
}
