import React from 'react';
import {useState,useEffect} from 'react';

import {BrowserRouter, Route, Routes } from "react-router-dom";
import { NavDropdown } from 'react-bootstrap';
import {Params} from './../params.jsx'
import Makondo from '../pages/makondo.jsx';
import Rooms from '../pages/rooms.jsx';
import Room from '../pages/room.jsx';
import MoreMonte from '../pages/moreMonte.jsx';
import Confid from '../pages/confident.jsx';
import Confirmation from '../pages/confirmation.jsx';
import ConfirmationProblem from '../pages/confirmation_problem.jsx';
import {room_1, room_2, room_3} from './../assets/rooms_img.jsx'
import {description_room1, description_room2, description_room3} from './../description'

import Booking from '../pages/booking.jsx';
import Start from '../pages/StartPage.jsx';
import pc1 from './../assets/images/pc1.PNG'
import pc2 from './../assets/images/pc2.PNG'
import pc3 from './../assets/images/pc3.PNG'
import pc4 from './../assets/images/pc4.PNG'
import pc5 from './../assets/images/pc5.PNG'
import menu from './../assets/images/menu.png'
import {Flags} from './../assets/images/flags.jsx'
import './../css/monte.css'
import './../css/reset.css'
import axios from 'axios'







export default function MainLayer (){


	const session_start = sessionStorage.getItem('lang') ? sessionStorage.getItem('lang')  : 'ru'
  const [Info, setInfo] = useState([{'price': '', 'currency': ''}, {'price': '', 'currency': ''},{'price': '', 'currency': ''}])

  useEffect(() => {
        const ax = axios.create({
        baseURL: 'http://localhost:5000/',
        timeout: 1000,
        headers: {"Access-Control-Allow-Origin": 'http://localhost:3000'}
    })
          ax.get('api/user/rooms')
            .then((response) => {
                 setInfo(response.data)
      
          })
        },[])

	const [lang, setLang] = useState(session_start)
    const onLang = (lng) => (e) => {
    		setLang(lng)

    	}

    	useEffect(() => {
    		sessionStorage.setItem('lang', lang)

    	},[lang])

    
        return (
        <div className="mx-2">
	        <div className="picture mt-3">
	          <img src={pc1} alt = "pc1"/>
	          <img src={pc2} alt = "pc2"/>
	          <img src={pc3} alt = "pc3"/>
	          <img src={pc4} alt = "pc4"/>
	          <img src={pc5} alt = "pc5"/>
	        </div>


	        <div className="first-head pt-3 pb-3">
	          <div className="fs-2 ms-5 w-75">
	            <div className="container-fluid ">
	              <div className="row">
	                <div className="col-md-6 head-span">   
	                  <span className="myfs-1 text-white fst-italic">Montenegro?</span>
	                </div>
	                <div className="col-md-6 mt-3">    
	                  <span className="myfs-2 text-white fst-italic ">{Params(lang).here}</span>
	                </div>
	              </div>  
	            </div>
	          </div>

	        	<div className="d-flex mt-3 me-md-5 me-3">
	            				<NavDropdown title={<img src={Flags(lang)} alt="flag"/>} id="basic-nav-dropdown">
							          <NavDropdown.Item  onClick={onLang('ru')}>ru</NavDropdown.Item>
							          <NavDropdown.Item onClick = {onLang('en')}>en</NavDropdown.Item>
							          <NavDropdown.Item onClick = {onLang('de')}>de</NavDropdown.Item>
						          	<NavDropdown.Item onClick = {onLang('sr')}>sr</NavDropdown.Item>
						        	</NavDropdown>


						        <NavDropdown title={<img src={menu} alt = "menu"/>} id="basic-nav-dropdown">
						          <NavDropdown.Item href="/rooms">{Params(lang).rooms}</NavDropdown.Item>
						          <NavDropdown.Item href="/makondo">{Params(lang).about}</NavDropdown.Item>
						          <NavDropdown.Item href="/more">{Params(lang).more}</NavDropdown.Item>
						          <NavDropdown.Item href="/confid">{Params(lang).confid}</NavDropdown.Item>
						        </NavDropdown>

	            </div>
						      
	          </div>
        

			     <BrowserRouter>
				     <Routes>
					        <Route path='/' element={<Start lang = {lang} link = {true}/> } />
					        <Route path='/makondo' element={<Makondo lang = {lang} link = {false}/> } />
					        <Route path='/rooms' element={<Rooms lang = {lang} link = {false} Info = {Info}/> } />
					        <Route path='/room_1' element={<Room number = '1' room = {room_1} description_room = {description_room1} lang = {lang} link = {false} Info = {Info}/> } />
					        <Route path='/room_2' element={<Room number = '2' room = {room_2} description_room = {description_room2} lang = {lang} link = {false} Info = {Info}/> } />
					        <Route path='/room_3' element={<Room number = '3' room = {room_3} description_room = {description_room3} lang = {lang} link = {false} Info = {Info}/> } />
					        <Route path='/booking' element={<Booking lang = {lang} link = {false} Info = {Info}/> } />
					        <Route path='/more' element={<MoreMonte lang = {lang} link = {false}/> } />
					        <Route path='/confid' element={<Confid lang = {lang} link = {false}/> } />
					        <Route path='/confirmation_ok' element={<Confirmation lang = {lang} link = {false} answer = {true}/> } />
					        <Route path='/confirmation_no' element={<Confirmation lang = {lang} link = {false} answer = {false}/> } />
					        <Route path='/confirmation_problem' element={<ConfirmationProblem lang = {lang}/> } />


				      </Routes>
        
      			</BrowserRouter>

		<div className="bg6 d-flex justify-content-center pt-4 pb-4 flex-wrap ">
          <div><a href="https://icons8.ru/icons/set/флаги" className="text-primary">flags from Icons8 </a>&nbsp; &nbsp;</div>
          <div>Copyright &copy; 2021 &nbsp; allof.best &nbsp; &nbsp;</div>
          <div>Made with Yii</div>
      	</div>
	</div>

        )
}